@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&family=Staatliches&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&family=Open+Sans:wght@300;600;700&display=swap);
/****** Elad Shechter's RESET *******/
/*** box sizing border-box for all elements ***/
*,
*::before,
*::after{box-sizing:border-box;}
a{text-decoration:none; color:inherit; cursor:pointer;}
button{background-color:transparent; color:inherit; border-width:0; padding:0; cursor:pointer;}
figure{margin:0;}
input::-moz-focus-inner {border:0; padding:0; margin:0;}
ul, ol, dd{margin:0; padding:0; list-style:none;}
h1, h2, h3, h4, h5, h6{margin:0; font-size:inherit; font-weight:inherit;}
p{margin:0;}
cite {font-style:normal;}
fieldset{border-width:0; padding:0; margin:0;}
:root {
  --primary-color: #4800bd;
  --primary-color-fade: rgba(72, 0, 189, 0.2);
  --secondary-color: #007c6d;
  --secondary-color-fade: #b3d8d3;
  --text-color: #050812;
  --weight-normal: 300;
  --weight-bold: 600;
}

body {
  font-family: "Poppins", Arial, Helvetica, sans-serif;
  line-height: 1.5;
  margin-top: 83px;
  font-weight: 300;
  font-weight: var(--weight-normal);
}

.contained-img {
  max-width: 100%;
}

.container {
  box-sizing: unset;
  width: 1278px;
  margin: 0 auto;
  padding: 0 calc(max(24px, calc(50vw - 639px)));
  overflow-x: hidden;
  max-width: calc(100% - 48px);
}

.color-primary {
  color: #4800bd !important;
  color: var(--primary-color) !important;
}

.margin-center {
  display: block;
  margin-left: auto !important;
  margin-right: auto !important;
}

.margin-down {
  margin-bottom: 56px;
}

.centered-text {
  text-align: center !important;
}

.highlight {
  background: rgba(69, 230, 210, 0.5);
  box-shadow: 0 0 0 2px rgba(69, 230, 210, 0.5);
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
  color: #050812;
  color: var(--text-color);
  font-size: 18px;
  font-weight: 300;
  font-weight: var(--weight-normal);
  line-height: 1.5;
}

h1 {
  font-size: 56px;
  font-weight: 600;
  font-weight: var(--weight-bold);
}

h2 {
  font-size: 36px;
}

h3 {
  font-weight: 600;
  font-weight: var(--weight-bold);
}

.nav-btn.active {
  color: #007c6d;
  color: var(--secondary-color);
}

.text-btn {
  text-decoration: none;
  font-size: 16px;
  transition: 0.1s;
  color: #4800bd;
  color: var(--primary-color);
  cursor: pointer;
}
.text-btn:visited,
.text-btn:active {
  color: #4800bd;
  color: var(--primary-color);
}
.text-btn:hover {
  color: #007c6d;
  color: var(--secondary-color);
  text-decoration: underline;
}
.text-btn.title {
  font-weight: 500;
}
.text-btn.with-img {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}
.text-btn.with-img img,
.text-btn.with-img svg {
  margin-left: 8px;
}

.btn {
  display: flex;
  position: relative;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  align-items: center;
  justify-content: center;
  padding: 8px 16px;
  border: solid #4800bd 1px;
  border: solid var(--primary-color) 1px;
  color: #4800bd;
  color: var(--primary-color);
  font-weight: 300;
  font-weight: var(--weight-normal);
  text-decoration: none;
  letter-spacing: 0.02em;
  cursor: pointer;
  background: white;
  box-shadow: 2px 2px #4800bd;
  box-shadow: 2px 2px var(--primary-color);
  transition: 0.1s;
}
.btn[data-sub-text]::after {
  content: attr(data-sub-text);
  font-weight: 300;
  font-weight: var(--weight-normal);
  margin-left: 16px;
  padding-left: 16px;
  border-left: solid #4800bd 2px;
  border-left: solid var(--primary-color) 2px;
  opacity: 0;
  transition: 0.3s;
}
.btn:hover {
  -webkit-transform: translate(2px, 2px);
          transform: translate(2px, 2px);
  box-shadow: 0 0 #4800bd;
  box-shadow: 0 0 var(--primary-color);
}
.btn:hover::after {
  opacity: 1;
}

.btn.btn-small {
  padding: 4px 16px;
}
.btn.icon-btn {
  padding: 4px;
}
.btn.btn-long {
  padding: 8px 40px;
}
.btn.btn-big {
  padding: 30px 44px;
  font-size: 32px;
  font-weight: 600;
  font-weight: var(--weight-bold);
  line-height: 1;
  justify-content: flex-start;
  border-width: 2px;
  box-shadow: 4px 4px #4800bd;
  box-shadow: 4px 4px var(--primary-color);
}
.btn.btn-big::before {
  border-width: 2px;
  top: 10px;
  left: 10px;
}
.btn.btn-big::after {
  margin-left: 24px;
  padding-left: 24px;
}
.btn.btn-big:hover {
  -webkit-transform: translate(4px, 4px);
          transform: translate(4px, 4px);
  box-shadow: 0 0 #4800bd;
  box-shadow: 0 0 var(--primary-color);
}
.btn img:not(:last-child) {
  margin-right: 8px;
}

.text-container-1 {
  width: 424px;
}

.text-container-2 {
  width: 464px;
}

.text-container-3 {
  width: 504px;
}

.divider {
  color: #4800bd;
  color: var(--primary-color);
  margin: 0 24px;
  opacity: 0.5;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}


header {
  background: white;
  border-bottom: 1px solid var(--primary-color-fade);
  padding: 24px;
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
}
header .logo-nav-section {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}
nav {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  position: relative;
}
nav .text-btn {
  margin-right: 24px;
}
nav .work-nav-links {
  display: none;
  flex-flow: column;
  padding: 18px 24px;
  background: #ffffff;
  box-shadow: 2px 2px var(--primary-color);
  border: solid 1px var(--primary-color);
  position: absolute;
  top: 40px;
  left: -16px;
}
nav .work-nav-links::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: -32px;
  height: 32px;
  width: 100%;
}
nav .work-nav-links .text-btn {
  margin-right: 0;
}
nav .work-nav-links .text-btn:not(:last-child) {
  margin-bottom: 24px;
}
header .contact-and-cv,
header .mobile-menu-icon,
header .mobile-menu-exit,
header .work-divider {
  display: none;
}

.work-btn:hover ~ .work-nav-links,
.work-nav-links:hover {
  display: flex;
}

footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: white;
  border-top: 1px solid var(--primary-color-fade);
  padding: 24px;
}
footer .contacts {
  display: flex;
  align-items: center;
}
footer .contact-btn {
  margin-right: 24px;
  font-weight: var(--weight-bold);
}
footer h4 {
  opacity: 0.5;
}
footer .socials {
  display: flex;
  grid-gap: 16px;
  gap: 16px;
}

.content-divider {
  height: 1px;
  background: #000;
  margin: 56px 0;
}

.main-img {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  max-height: calc(min(70vh, 675px));
  border-bottom: solid 1px #000;
}
.main-img img {
  width: 100vw;
  height: auto;
  display: block;
}

.summery-text-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 88px;
  -webkit-column-gap: 88px;
          column-gap: 88px;
  grid-row-gap: 40px;
  row-gap: 40px;
  margin-top: 40px;
}
.summery-text-container .titles {
  grid-column: 1 / 3;
}
.summery-text-container .titles h1 {
  line-height: 1;
  margin-bottom: 16px;
}
.summery-text-container .titles h4 {
  line-height: 1;
}
.summery-text-container .text-block h3 {
  margin-bottom: 24px;
}

.img-carousel {
  display: none;
  grid-auto-columns: 100vw;
  justify-items: center;
  overflow-x: auto;
  -ms-scroll-snap-type: x mandatory;
      scroll-snap-type: x mandatory;
  position: relative;
}
.img-carousel::-webkit-scrollbar {
  display: none;
}
.img-carousel .dots-cover {
  position: -webkit-sticky;
  position: sticky;
  bottom: 14px;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 14px;
  gap: 14px;
  z-index: 10;
  background: white;
  mix-blend-mode: screen;
  box-shadow: 0 0 0 1px white;
}
.img-carousel .dots-cover div {
  height: 14px;
  width: 14px;
  border-radius: 50%;
  background: black;
}
.img-carousel .item {
  grid-row: 1 / 2;
  scroll-snap-align: center;
  width: 100%;
}
.img-carousel .item img {
  display: block;
  margin: 0 auto;
}
.img-carousel .item .dots {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 14px;
  gap: 14px;
  background: var(--secondary-color-fade);
}
.img-carousel .item .dots div {
  height: 14px;
  width: 14px;
  border-radius: 50%;
  background: var(--secondary-color-fade);
}
.img-carousel .item .dots div.selected {
  background: var(--secondary-color);
}

.centered-title-and-p h3 {
  text-align: center;
  margin-bottom: 24px;
  text-transform: uppercase;
}
.centered-title-and-p p {
  text-align: center;
  margin: 0 auto;
}

.left-title-and-p h3 {
  margin-bottom: 24px;
}

.color-palette h3 {
  margin-bottom: 24px;
}
.color-palette .colors {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  padding-top: 8px;
}
.color-palette .colors div {
  display: flex;
  flex-flow: column;
  margin: 0 -12px;
  align-items: center;
}
.color-palette .colors div::before {
  content: "";
  display: block;
  height: 180px;
  width: 180px;
  border-radius: 50%;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  margin-bottom: 24px;
}
.color-palette .colors div::after {
  content: attr(data-color-name);
  display: block;
  width: 100%;
  font-size: 12px;
  text-align: center;
}

.another-app-btn-section .arrow-img {
  display: block;
  margin: 24px auto;
}
.another-app-btn-section .btn-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 24px;
  gap: 24px;
  margin: 0 auto 32px auto;
}
.another-app-btn-section .btn-container .btn {
  justify-content: flex-start;
  min-width: 320px;
  padding: 16px;
}
.another-app-btn-section .btn-container .btn::after {
  display: block;
  opacity: 1;
}



